<template>
  <div class="note-sidebar">
    <span class="btn add-note" @click="addNote"></span>
    <el-dropdown class="notebook-title" @command="handleCommand" placement="bottom">
      <span class="el-dropdown-link">
        {{curBook.title}}<i class="iconfont icon-down"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item v-for="notebook in notebooks" :key="notebook.id" :command="notebook.id">{{notebook.title}}</el-dropdown-item>
        <el-dropdown-item command="trash"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <div class="menu">
      <div></div>
      <div></div>
    </div>
    <ul class="notes">
      <li v-for="note in notes">
        <router-link :to="`/note?notebookId=${curBook.id}&noteId=${note.id}`">
          <span class="date">{{ note.updatedAtFriendly }}</span>
          <span class="title">{{ note.title }}</span>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import Notebooks from "@/apis/notebooks";
import Notes from '@/apis/notes';
import Bus from '@/helpers/bus'


window.Notes = Notes


export default {
  created() {
    Notebooks.get()
      .then(res => {
        this.notebooks = res.data
        this.curBook = this.notebooks.find(notebook => notebook.id == this.$route.query.notebookId)
          || this.notebooks[0] || {}
        return Notes.get({ notebookId: this.curBook.id })
      }).then(res => {
        this.notes = res.data
        this.$emit('update:notes', this.notes)
        Bus.$emit('update:notes', this.notes)
      })
  },
  props:['curNote'],

  data() {
    return {
      notebooks: [],
      notes: [],
      curBook:{}
    };
  },
  methods: {
    handleCommand(notebookId) {
      if(notebookId == 'trash') {
        return this.$router.push({path: '/trash'})
      }
      this.curBook = this.notebooks.find(notebook => notebook.id == notebookId)
      Notes.get({notebookId})
        .then(res => {
          this.notes = res.data
          this.$emit('update:notes', this.notes)
        })
    },
    addNote() {
      Notes.add({notebookId: this.curBook.id})
        .then(res => {
          this.notes.unshift(res.data)
        })
    }
  },
};
</script>



<style lang="less">

@import url(../assets/css/notesidebar.less);


</style>


