<template>
  <span :title="username">{{slug}}</span>
</template>

<script>
import Auth from '@/apis/Auth'
import Bus from '@/helpers/bus'

export default {
  data() {
    return {
      username: ''
    }
  },

  created() {
    Bus.$on('userInfo', user => {this.username = user.username} )
    Auth.getInfo()
    .then(res => {
      console.log(res)
      if(res.isLogin) {
        return this.username = res.data.username
      }
    })
  },

  computed: {
    slug() {
      return this.username.charAt(0)
    }
  }
}

</script>

<style scoped>

span {
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 32px;
  border-radius: 50%;
  background-color: #eee;
  color: olivedrab;
  margin-top: 15px;
  font-weight: bold;
  /**/

}
</style>


