import request from '@/helpers/request'
import  {friendlyDate}  from '@/helpers/util'



const URL = {
  GET: '/notebooks',
  ADD: '/notebooks',
  EDIT: '/notebooks/:notebookId',
  DELETE : '/notebooks/:notebookId'
}

export default {
  get() {
    return new Promise((resolve, reject) => {
      request(URL.GET)
        .then(res => {
          res.data = res.data.sort((notebook1, notebook2) => notebook1.createAt < notebook2.createAt ? 1 : -1)
          res.data.forEach(notebook => {
            notebook.friendlyCreatedAt = friendlyDate(notebook.createdAt)
          })
          resolve(res)
        }).catch(err => {
          reject(err)
        })
    })

  },

  add({title = ''} = {title : ''}) {
      return request(URL.ADD, 'POST', {title})
  },

  edit(notebookId, {title = ''} = {title : ''}) { 
      return request(URL.EDIT.replace(':notebookId', notebookId), 'PATCH', {title})
  },

  delete(notebookId) {
    return request(URL.DELETE.replace(':notebookId', notebookId), 'DELETE')
  }

}


