<template>
  <div class="modal">
    <div class="wrapper">
      <div class="container">
        <div class="img"></div>
        <div class="form">
          <h3 @click="showLogin"></h3>
          <transition name="slide">
            <div v-bind:class="{show:isShowLogin}" class="login">
              <input type="text" @input="loginUsernameValid" v-model="login.username" placeholder="">
              <input type="password" @input="loginPasswordValid" v-model="login.password" placeholder="">
              <p v-bind:class="{error: login.isError}">{{login.notice}}</p>
              <div class="button" @click="onLogin"></div>
            </div>
          </transition>
          <h3 @click="showRegister"></h3>
          <transition name="slide">
          <div v-bind:class="{show:isShowRegister}" class="register">
            <input type="text" @input="registerUsernameValid" v-model="register.username" placeholder="">
            <input type="password" @input="registerPasswordValid" v-model="register.password" placeholder="">
            <p v-bind:class="{error: register.isError}">{{register.notice}}</p>
            <div class="button" @click="onRegister"></div>
          </div>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>







<script>
import Auth from '@/apis/Auth'
import Bus from '@/helpers/bus'



export default {
  data () {
    return {
      isShowLogin: true,
      isShowRegister: false,
      login: {
        username: '',
        password: '',
        notice: '',
        isError: false
      },
      register: {
        username: '',
        password: '',
        notice: '',
        isError: false
      },
    }
  },
  methods: {
    showRegister() {
      this.isShowRegister = true
      this.isShowLogin = false
    },
    showLogin() {
      this.isShowRegister = false
      this.isShowLogin = true
    },
    onLogin() {
      console.log('...')
      if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {
        this.login.isError = true;
        this.login.notice = '3-15'
        return; 
        }
      if(!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true;
        this.login.notice = '6-16'
        return;
      }


      Auth.login({username: this.login.username, password: this.login.password})
        .then(data => {
          this.login.isError = false
          this.login.notice = ''
          Bus.$emit('userInfo', {username: this.login.username})
          console.log(`start login..., username: ${this.login.username} , password: ${this.login.password}`)
          this.$router.push({path: 'notebooks'})
        }).catch(data => {
          this.login.isError = true
          this.login.notice = data.msg
        })
    },

    onRegister() {
      console.log('...')
      if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
        this.register.isError = true;
        this.register.notice = '3-15'
        return; 
        }
      if(!/^.{6,16}$/.test(this.register.password)) {
        this.register.isError = true;
        this.register.notice = '6-16'
        return;
      }
      this.register.isError = false
      this.register.notice = ''
      Bus.$emit('userInfo', {username: this.login.username})
      console.log(`start register..., username: ${this.register.username} , password: ${this.register.password}`)

      Auth.register({username: this.register.username, password: this.register.password})
        .then(data => {
          this.register.isError = false
          this.register.notice = ''
          console.log(`start register..., username: ${this.register.username} , password: ${this.register.password}`)
          this.$router.push({path: 'notebooks'})
        }).catch(data => {
          this.register.isError = true
          this.register.notice = data.msg
        })

    },

//
//
//
    loginUsernameValid(){
      if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {
        this.login.isError = true;
        this.login.notice = '3-15'
        return
      }
      this.login.isError = false;
      this.login.notice = '';

    },

    loginPasswordValid() {
      if(!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true;
        this.login.notice = '6-16'
        return;
      }
      this.login.isError = false;
      this.login.notice = '';
    },

    registerUsernameValid(){
      if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
        this.register.isError = true;
        this.register.notice = '3-15'
        return
      }
      this.register.isError = false;
      this.register.notice = '';

    },

    registerPasswordValid() {
      if(!/^.{6,16}$/.test(this.register.password)) {
        this.register.isError = true;
        this.register.notice = '6-16'
        return;
      }
      this.register.isError = false;
      this.register.notice = '';
    }
  }
}
</script>







<style lang="less" scoped>
.modal {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .7);
  display: table;
  transition: opacity .3s ease;
}

.wrapper {
  display: table-cell;
  vertical-align: middle;
}
.container {

  width: 800px;
  height: 500px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;


  .img {
    flex: 1;
    background: #36bc64 url(//cloud.hunger-valley.com/17-12-13/38476998.jpg-middle) center center no-repeat;
    background-size: contain;
  }
  .form {
    width: 270px;
    border-left: 1px solid #ccc;
    // overflow: hidden;

    h3 {
      padding: 10px 20px;
      font-weight: normal;
      font-size: 20px;
      // border-top: 1px solid #eee;
      cursor: pointer;

    &:nth-of-type(1){
        border-bottom: 1px solid #eee;
      }
    }

  .button {
    background-color: #2bb964;
    height: 36px;
    line-height: 36px;
    text-align: center;
    font-weight: bold;
    color: #fff;
    border-radius: 4px;
    margin-top: 18px;
    cursor: pointer;
  }

    .login, .register {
      padding: 0px 20px;
      border-top: 1px solid #eee;
      height: 0px;
      overflow: hidden;
      transition: height .4s;

      &.show {
        height: 193px;
      }



      input {
        display: block;
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 6px;
        border-radius: 4px;
        border: 1px solid #ccc;
        outline: none;
        font-size: 14px;
        margin-top: 10px;
      }

      input:focus {
        border: 3px solid #9dcaf8;
      }

      p {
        font-size: 12px;
        margin-top: 10px;
        color: #444;
      }
      .error {
        color: red;
      }
    }
    .login {
      border-top: 0;
    }

  }


}
</style>



