<template>
  <div id="notebooklist">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate"
        ><i class="iconfont icon-plus"></i></a
      >
    </header>
    <main>
      <div class="layout">
        <h4>({{ notebooks.length }})</h4>
        <div class="list">
          <router-link
            v-for="notebook in notebooks"
            :to="`/note?notebookId=${notebook.id}`"
            class="notebook"
          >
            <div>
              <span class="iconfont icon-notebook"></span>{{ notebook.title }}
              <span>{{ notebook.noteCounts }}</span>
              <span class="edit" @click.stop.prevent="onEdit(notebook)"
                ></span
              >
              <span class="delete" @click.stop.prevent="onDelete(notebook)"
                ></span
              >
              <span class="date">{{ notebook.friendlyCreatedAt }}</span>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Auth from "@/apis/Auth";
import Notebooks from "@/apis/notebooks";
import { friendlyDate } from "@/helpers/util";


window.Notebooks = Notebooks;

export default {
  data() {
    return {
      notebooks: [],
    };
  },
  created() {
    Auth.getInfo().then((res) => {
      if (!res.isLogin) {
        this.$router.push({ path: "/login" });
      }
    });
    Notebooks.get().then((res) => {
      this.notebooks = res.data;
      console.log(res.data);
    });
  },
  methods: {
    onCreate() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: ", 30",
      })
        .then(({ value }) => {
          return Notebooks.add({ title: value });
        })
        .then((res) => {
          console.log(res);
          res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt);
          this.notebooks.unshift(res.data);
          this.$message({
            type: "success",
            message: res.msg,
          });
        });
    },
    onDelete(notebook) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        return Notebooks.delete(notebook.id);
      }).then(res=>{
        this.notebooks.splice(this.notebooks.indexOf(notebook), 1);
          this.$message({
          type: "success",
          message: "!",
        });
      }).catch(err => {
        this.$message(err.msg)
      })
    },
    onEdit(notebook) {
      let title = "";
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /^.{1,30}$/,
        inputValue: notebook.title,
        inputErrorMessage: ", 30",
      })
        .then(({ value }) => {
           title = value;
          return Notebooks.edit(notebook.id, { title });
        })
        .then((res) => {
          console.log(res);
          notebook.title = title;
          this.$message({
            type: "success",
            message: res.msg,
          });
        });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import url(../assets/css/notebooklist);
</style>



