<template>
  <div id="app">

    <Sidebar></Sidebar>

    <router-view/>

    <p></p>
  </div>
</template>

<script>
import Sidebar from '@/components/Sidebar'


export default {
  name: 'App',
  components: {
    Sidebar
  }
  
}
</script>

<style>
@import '//at.alicdn.com/t/font_496303_kqrjhri8l25d0a4i.css';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body, #app {
  height: 100%;
}
/*  */

body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 14px;
  -moz-osx-font-smoothing: grayscale; /**/
  background-color: #eee;
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
}

.btn {
  color: #eee;
  font-size: 18px;
  padding: 2px 4px;
  background-color: rgb(150, 185, 125);
}

#app {
  display: flex;
  align-items: stretch;
}
</style>



