<template>
  <div id="sidebar">
    <Avatar></Avatar>
    <div class="icons">
      <router-link to="/note" title=""><i class="iconfont icon-note"></i></router-link>
      <router-link to="/notebooks" title=""><i class="iconfont icon-notebook"></i></router-link>
      <router-link to="/trash" title=""><i class="iconfont icon-trash"></i></router-link>
    </div>
    <div class="logout" >
      <i class="iconfont icon-logout" @click="logout"></i>
    </div>
  </div>
</template>

<script>
import Avatar from '@/components/Avatar'
import Auth from '@/apis/Auth'

export default {
  components: {
    Avatar
  },
  methods: {
    logout() {
      Auth.logout()
        .then(data => {
          this.$router.push({path: '/login'})
          console.log(data)
          
        })
    }
  }
}
</script>

<style lang="less" scoped>
  #sidebar {
    position: relative;
    width: 56px;
    text-align: center;
    background-color: rgb(150, 185, 125);

    .icons {
      margin-top: 15px;
    }

    a {
      padding: 6px 0;
      display: block;
    }

    .router-link-active {
      background-color: rgba(250,250,250,.2);
    }


    .logout {
      position: absolute;
      bottom: 20px;
      width: 100%;
      text-align: center;
      cursor: pointer;
    }

    .iconfont {
      color: #fff;
    }
}
</style>


