<template>
  <div id="note" class="detail">
    <NoteSidebar @update:notes="val=>notes=val"></NoteSidebar>
    <div class="note-detail">
      <div class="note-empty" v-show="!curNote.id"></div>
      <div class="note-fullscreen "v-show="curNote.id">
        <div class="note-bar">
          <span>: {{curNote.createdAtFriendly}}</span>
          <span>: {{curNote.updatedAtFriendly}}</span>
          <span>{{statusText}}</span>
          <span class="iconfont icon-delete" @click="deleteNote"></span>
          <span class="iconfont icon-fullscreen" @click="isShowPreview=!isShowPreview"></span>
        </div>
        <div class="note-title">
          <input type="text" v-model:value="curNote.title" @input="updateNote" @keydown="statusText='...'" placeholder="">
        </div>
        <div class="editor">
          <textarea v-show="isShowPreview" v-model:value="curNote.content" @input="updateNote" @keydown="statusText='...'" placeholder="markdown"></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="!isShowPreview"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from '@/apis/Auth'
import NoteSidebar from '@/components/NoteSidebar'
import Bus from '@/helpers/bus'
import _ from 'lodash'
import Notes from '@/apis/notes';
import MarkdownIt from 'markdown-it'

let md = new MarkdownIt()

// var result = md.render('# markdown-it rulezz!');
export default {
  data () {
    return {
      curNote: {},
      notes:[],
      statusText: '',
      isShowPreview: true
    }
  },
  created() {
    Auth.getInfo()
    .then(res => {
      if(!res.isLogin) {
        this.$router.push({path: '/login'})
      }
    })
    Bus.$once('update:notes', val => {
      this.curNote = val.find(note => note.id == this.$route.query.noteId) || {}
    })
  },
  computed: {
    previewContent() {
      return md.render(this.curNote.content || '');
    }
  },
  methods:{
    updateNote: _.debounce(function() {
      Notes.edit({noteId: this.curNote.id}, {title: this.curNote.title ,content: this.curNote.content})
        .then(data => {
          this.statusText = ''
        }).catch(data => {
          this.statusText = ''
        })
    }, 300),
    deleteNote() {
      Notes.delete({ noteId: this.curNote.id})
        .then(data => {
          this.$message.success(data.msg)
          this.notes.splice(this.notes.indexOf(this.curNote), 1)
          this.$router.replace({path: '/note'})
        })
    }
  },
  beforeRouteUpdate(to, from, next) {
    console.log(to, from)
    this.curNote = this.notes.find(note=>note.id == to.query.noteId) || {}
    next()
  },
  components: {
    NoteSidebar
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import url(../assets/css/note-detail.less);
#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
</style>



