<template>
  <div class="hello">
    <h1>noteId:{{$route.query.noteId}}</h1>

  </div>
</template>

<script>
import Auth from '@/apis/Auth'

export default {
  data () {
    return {
      msg: ''
    }
  },  
  created() {
    Auth.getInfo()
  .then(res => {
    if(!res.isLogin) {
      this.$router.push({path: '/login'})
    }
  })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



